	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Oldest German Blog?","date":"16 Nov 2004","slug":"/2004-11-16-oldest-german-blog/","tags":"Y2004"},"html":"<p>I'm just wondering, what's the oldest active German weblog? How many German blogs that were early adopters of first wave of blogging services like <a href=\"http://www.blogger.com/\">Blogger</a> (Aug 1999), <a href=\"http://www.diaryland.com/\">Diaryland</a> (Sept 1999), and <a href=\"http://www.editthispage.com/\">EditThisPage</a> (Dec 1999) are still active? </p>\n<p>I'm familiar with the EditThisPage crowd... <a href=\"http://andrea.editthispage.com/\">Andrea</a> started in January 2000 and is still going strong. I think her husband <a href=\"http://www.spicynoodles.net/\">Andr&eacute;</a> (who worked for Userland at the time) was using Manila before EditThisPage went public, but now he now posts only once every other year, so I'm not sure he can be called active. Are there any other German '99ers?</p>\n<p><strong>Update:</strong> Ah, I had forgotten <a href=\"http://traumwind.de/blog/\">Martin</a> was also among the very first ETPers. And <a href=\"http://traumwind.editthispage.com/discuss/msgReader$1\">his first post</a> was just a few days before Andrea's.</p>\n<p>PapaScott was an early adopter of EditThisPage, but I'm not German, so I don't qualify.</p>"},"next":{"fields":{"title":"Norwegians now favor joining EU","date":"16 Nov 2004","slug":"/2004-11-16-norwegians-now-favor-joining-eu/"}},"previous":{"fields":{"title":"Rotten Compromise","date":"16 Nov 2004","slug":"/2004-11-16-rotten-compromise/"}}},{"node":{"fields":{"title":"Norwegians now favor joining EU","date":"16 Nov 2004","slug":"/2004-11-16-norwegians-now-favor-joining-eu/","tags":"Y2004"},"html":"<p><a title=\"A Fistful of Euros: Twice Bitten Thrice Shy?\" href=\"http://fistfulofeuros.net/archives/000934.php\">Poll shows Norwegians now favor joining EU.</a> Think of all the oil money we can redistribute!</p>"},"next":{"fields":{"title":"I Like the Poof","date":"16 Nov 2004","slug":"/2004-11-16-i-like-the-poof/"}},"previous":{"fields":{"title":"Oldest German Blog?","date":"16 Nov 2004","slug":"/2004-11-16-oldest-german-blog/"}}},{"node":{"fields":{"title":"I Like the Poof","date":"16 Nov 2004","slug":"/2004-11-16-i-like-the-poof/","tags":"Y2004"},"html":"<p>I made the mistake of showing Christopher how to <a title=\"Tiny Pineapple :: Archives :: \"I like the poof.\"\" href=\"http://www.tinypineapple.com/archives/2002/10/i_like_the_poof.html\">poof items from the dock on my PowerBook. Now he wants to do it all the time.</p>"},"next":{"fields":{"title":"worst of both worlds","date":"15 Nov 2004","slug":"/2004-11-15-worst-of-both-worlds/"}},"previous":{"fields":{"title":"Norwegians now favor joining EU","date":"16 Nov 2004","slug":"/2004-11-16-norwegians-now-favor-joining-eu/"}}}],"pathPrefix":"","first":false,"last":false,"index":382,"pageCount":1101}}

